<template>
    <section>
    <h1>Wordle helper because I've had enough</h1>
    <form>
        <div v-for="(letter, index) in current_word" :key="index" class='letter-input'>
            <input v-model="current_word[index]" v-on:keydown ="isLetter($event)" v-on:input="focusNext($event)" type="text" v-bind:autofocus="index===0" maxlength="1">
        </div>
    </form>

    <br>
    <button v-on:click="resetWord">Reset Word</button>

    <h2>Discarded Letters</h2>
    <p>Enter as a space seperated list</p>
    <textarea v-model="discard_letters">
    </textarea>

    <h2>Contains Letters</h2>
    <p>Enter as a space seperated list</p>
    <textarea v-model="contains_letters">
    </textarea>

    <h2>Possible Words</h2>
    <ul>
        <li v-for="(word, index) in usable_words" :key="index">{{word}}</li>
    </ul>
    </section>
</template>

<script>
import words from '../json/words.json'
export default {
    name: 'WordleHelper',
    data() {
        return {
            discard_letters : '',
            contains_letters: '',
            current_word : new Array(5),
            all_words: words,
        }
    }, 
    computed: {
        discard_array: function() {
            return this.discard_letters.split(' ').filter( (value, index, self)=> {
                return self.indexOf(value) === index;
            });
        },
        contains_array: function() {
            return this.contains_letters.split(' ').filter( (value, index, self)=> {
                return self.indexOf(value) === index;
            });
        },
        usable_words : function() {
            let limit  = 100;
            let count = 0;

            let output = [];

            for (let word of this.all_words) {
                if (count > limit) {
                    break;
                }
                let match_characters = true; 
                //First check all the letters against the currently entered
                
                for (let letter in word) {
                    if ( this.current_word[letter] && word[letter] != this.current_word[letter].toLowerCase() ) {
                        match_characters = false;
                        break;
                    }

                    //Check that there are no discard letters.
                    if (this.discard_array.includes(word[letter])) {
                        match_characters = false;
                        break;
                    }
                }

                let contains_all_characters = true;
                //Inelegant, to this someway else if can
                for (let letter of this.contains_array) {
                    if (! word.includes(letter)) {
                        contains_all_characters = false;
                    }
                }

                if (match_characters && contains_all_characters) {
                    output.push(word);
                    count++;
                }
            }

            return output;
        }
    },
    methods: {
        focusNext(e) {
            //console.log(e);
            if (e.data) {
                //Go forward without overshooting
                const inputElements = Array.from(e.target.form.querySelectorAll("input[type='text']"));
                const currentIndex = inputElements.indexOf(e.target);

                //If the element is the last one don't overshoot. 
                if (currentIndex == inputElements.length - 1 ) {
                    return;
                }
                
                const nextElement = inputElements[currentIndex < inputElements.length - 1 ? currentIndex + 1 : 0];

                if (nextElement) {
                    nextElement.focus();
                    nextElement.select();
                }
            }
        },
        focusPrevious(e) {
            //Go back without overshooting
            const inputElements = Array.from(e.target.form.querySelectorAll("input[type='text']"));
            const currentIndex = inputElements.indexOf(e.target);
            
            if (currentIndex == 0) {
                return;
            }

            const nextElement = inputElements[currentIndex > 0 ? currentIndex - 1 : (inputElements.length - 1)];

            if (nextElement) {
                    nextElement.focus();
                    nextElement.select();
            }
        },
        nextElement(e) {
            const inputElements = Array.from(e.target.form.querySelectorAll("input[type='text']"));
            const currentIndex = inputElements.indexOf(e.target);
            const nextElement = inputElements[currentIndex < inputElements.length - 1 ? currentIndex + 1 : 0];
            
            if (nextElement) {
                nextElement.select();
                nextElement.focus();
            }
        },
        previousElement(e) {
            const inputElements = Array.from(e.target.form.querySelectorAll("input[type='text']"));
            const currentIndex = inputElements.indexOf(e.target);
            const nextElement = inputElements[currentIndex > 0 ? currentIndex - 1 : (inputElements.length - 1)];

            if (nextElement) {
                nextElement.select();
                nextElement.focus();
                    
            }
        },
        isLetter(e) {
            let char = String.fromCharCode(e.keyCode); // Get the character

            const unprevented_keycodes = [9, 16];

            if (e.keyCode == 8 || e.keyCode == 46) {
                
                //Should target the model and not the dom
                const inputElements = Array.from(e.target.form.querySelectorAll("input[type='text']"));
                const currentIndex = inputElements.indexOf(e.target);
                
                this.current_word[currentIndex] = '';
                
                if (!e.target.value) {
                    this.focusPrevious(e);
                }
                
            } else if(e.keyCode == 37) {
                //left arrow
                this.previousElement(e);

            } else if(e.keyCode == 39) {
                //right arrow
                this.nextElement(e);
            } else if(/^[A-Za-z]+$/.test(char)) {
                return true; // Match with regex 
            } else if (unprevented_keycodes.includes(e.keyCode)) {
                return true; //In keys we allow 
            } else {
                e.preventDefault();
                return false;
            }
        },
        resetWord() {
            this.current_word = this.current_word.map(()=>{

                return '';
            });
        }
    }
}
</script>

<style scoped>
    form {
        display: flex;
        margin: auto;
        gap: 1rem;
        width: fit-content;
        justify-content: space-between;
    }

    form input {
        font-size: 1.5rem;
        width: 2rem;
    }

    ul {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }

    ul li {
        list-style: none;
    }
</style>